.services{
    background-color: #e9f5f9;
    background-color: #eaf5f9;
    padding: 95px 0;
    position: relative;
    &__wrapper{
        background: #fff;
        overflow: hidden;
        position: relative;
    }
    &__header{
        p{
            font-size: 30px;
            max-width: 60%;
            line-height: 1.5;
            margin-bottom: 4rem;
            color: var(--c-title);
        }
    }
    &__item{
        background-color: #fff;
        height: 100%;
        padding: 60px 90px;
        overflow: hidden;
        &-title{
            font-size: 50px;
            font-weight: 700;
            margin-bottom: 7rem;
        }
        &-content{
            display: flex;
            flex-direction: column;
            height: 100%;
            p{
                font-size: var(--fz-s);
                color: var(--c-def);
            }
        }
        &-subtitle{
            color: var(--c-title);
            font-size: var(--fz-l);
            text-transform: initial;
            margin-bottom: 3rem;
        }
        
        .row{
            height: 100%;
        }
    }

    .services__left{
        display: flex;
        flex-direction: column;
    }
    .slider__btns-container{
        margin-top: auto;
    }

    .mySwiper{
        padding-top: 40px;
        padding-left: 90px;
        padding-right: 40px;
        padding-bottom: 10px;

        .swiper-slide-thumb-active{
            .thumb__item{
                span{
                    color: var(--c-primary);
                    &::before{
                        background: var(--c-primary);
                    }
                }
            }
        }
    }
    .thumb__item{
        cursor: pointer;
        span{
            display: flex;
            align-items: center;
            gap: 10px;
            font-size: 14px;
            transition: 0.3s linear;
            color: var(--c-def);
            &::before{
                content: '';
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: var(--c-white);
                transition: 0.3s linear;
            }
        }

        &:hover{
            span{
                color: var(--c-secondary);
                &::before{
                    background-color: var(--c-secondary);
                }
            }
        }
    }



    .slider__btns-container{
        display: flex;
        align-items: baseline;
        gap: 1rem;
    }
    .swiper-pagination-current{
        color: var(--c-primary);
        font-weight: 700;
    }
    .swiper-pagination{
            color: #eaf5f9;
    }
    .swiper-pagination-total{
        color: var(--c-title);
        font-weight: 700;
    }
    .count{
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(30%,-50%);
        color: var(--c-primary);
        font-size: 300px;
        font-weight: 700;
        transition: 0.3s linear;
        z-index: 2;
        &.move{
            transform: translate(100%,-50%);
        }
    }
    .swiper-slide{
        overflow: hidden;
        height: 100%;
    }
    
}